@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600;700;800&display=swap);
:root {
    --color--bg: black;
    --color-white: white;
   --color-olive-100: rgb(108 193 182);
   --color-gray-400:rgb(156 163 175);
   --color-gray-300:rgb(210, 210, 210);

   --transition: all 300ms ease;

   --container-width-lg: 89%;
   --container-width-md: 92%;
   --form-width: 50%;

   --radius-1: 0.3rem;
   --radius-2: 0.6rem;
   --radius-3: 1rem;
   --radius-4: 2rem;
}

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    font-family: "Poppins" , sans-serif;
    background: var(--color--bg);
    font-size: 0.9rem;
    line-height: 1.6;
    color: var(--color-white);
}

a {
    color: var(--color-gray-300);
    text-decoration: none;
}

ul {
    list-style: none;
}

h1 {
    color: var(--color-olive-100);
    line-height: 1.3;
    font-size: 1.5rem;
}

h2, h3, h4, h5, h6 {
    color: var(--color-olive-100);
    line-height: 1.4;
    font-size: 0.9rem;
}

img {
    display:block;
    object-fit: cover;
    width: 100%;
}

section {
    margin-top: 6rem;
}

.container {
    width: var(--container-width-lg);
    margin-inline: auto;
}

.btn {
    display: inline-block;
    width: -moz-fit-content;
    width: fit-content;
    padding: 0.5rem 1rem;
    border-radius: var(--radius-2);
    background: var(--color--bg);
    font-weight: 500;
    cursor: pointer;
    transition: var(--transition);
}

.btn.primary {
    background: var(--color-olive-100);
    color: var(--color-white);
}

.btn.sm {
    font-size: 0.8rem;
    padding: 0.3rem 0.7rem;
    font-weight: 400;
}

.btn.danger {
    background: var(--color--bg);
    color: var(--color-white);
}

.btn:hover {
    background: var(--color-gray-300);
    color: var(--color-white);
}

.center {
    text-align: center;
    padding-top: 5rem;
    margin-inline: center;
    display: block;
    width: 100%;
}

.error-page h2 {
    margin-top: 3rem;
}

/*=========================NAVBAR=======================*/
nav {
    width: 100vw;
    height: 5rem;
    display: grid;
    place-items: center;
    background: var(--color--bg);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    border-bottom: 4px solid var(--color--bg);
    -webkit-backdrop-filter: blur(20px);
            backdrop-filter: blur(20px);
}

.nav__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}
.nav__logo {
    width: 4rem;
    display: block;
}

.nav__menu {
    display: flex;
    align-items: center;
    grid-gap: 3rem;
    gap: 3rem;
}

.nav__toggle-btn {
    display: none;
}




/*===========================================FOOTER============================================*/
footer {
    background: var(--color--bg);
    margin-top: 5rem;
    padding-top: 6rem;
    color: var(--color-white);
}

.footer__categories {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 1.6rem;
    gap: 1.6rem;
    border-top: 1px 3px solid var(--color-gray-300);
    margin-bottom: 4rem;
}

.footer__categories a {
    background: rgb(133, 133, 133);
    color: var(--color-white);
    padding: 0.6rem 1.4rem;
    border-radius: var(--radius-2);
    transition: var(--transition);
}

.footer__categories a:hover {
    background: var(--color-olive-100);
    color: var(--color-white);
}

.footer__copyright {
    text-align: center;
    border-top: 2px solid var(--color-gray-400);
    padding: 1rem;
    color: var(--color-white);
    font-weight: 300;
}




/*=======================================Home===========================*/
/*=======================================POSTS============================*/
.posts__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 4rem;
    gap: 4rem;
    margin-top: 15rem;
}

.post {
    background: var(--color--bg);
    padding: 1rem;
    border-radius: var(--radius-4);
    padding-bottom: 2rem;
    transition: var(--transition);
    cursor: default;
}

.post:hover {
    box-shadow: 0 1.5rem 2rem rgba(--color-olive-600);
}

.post__thumbnail {
    border-radius: var(--radius-3);
    overflow: hidden;
    height: 10rem;
}

.post__content {
    margin-top: 1.9rem;
}

.post__content h3 {
    margin: 1rem 0rem 0.6rem;
}

.post__footer {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: 1rem;
}

.post__author {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: flex-start;
}

.post__author-avatar {
    width: 2.9rem;
    aspect-ratio: 1/1;
    border-radius: var(--radius-1);
    overflow: hidden;
}

/*=======================================================POST DETAIL ======================================================*/
.post-detail {
    width: 100%;
    background: var(--color--bg);
    margin-inline: auto;
    padding: 2rem 0;
}

.post-detail__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1.5rem;
}

.post-detail__buttons {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: center;
}

.post-detail__thumbnail {
    margin: 1rem 0;
    height: -moz-fit-content;
    height: fit-content;
    max-height: 30rem;
    overflow: hidden;
}

.post-detail p {
    margin-bottom: 0.8rem;
}

/*===============================================REGISTER & FORM STYLES =============================================*/
.form {
    display: flex;
    flex-direction: column;
    grid-gap: 0.8rem;
    gap: 0.8rem;
}

input, select, textarea {
    width: 100%;
    padding: 0.6rem 1rem;
    border-radius: var(--radius-1);
    resize: none;
}

.form__error-message {
    background: var(--color--bg);
    color: var(--color-white);
    font-size: 0.8rem;
    padding: 0.6rem 1rem;
    border-radius: var(--radius-1);
    display: block;
    margin-bottom: 1rem;
    margin-top: 0.6rem;
}

.register, .login, .create-post {
    width: var(--form-width);
    margin-inline: auto;
    height:calc(100vh - 12rem);
    display: grid;
}

.register h2, .login h2, .create-post h2 {
    margin-bottom: 2rem;
}

.register small, .login small {
    display: block;
    margin-top: 1.5rem;
    font-size: 0.9rem;
}

.register small a, .login small a {
    color: rgb(150, 150, 250);
}

/* custom style for react quill */
.qi-editor {
    overflow: scroll;
    background: var(--color--bg);
    height: 10rem !important;
}





/*===============================================AUTHORS================================================================*/
.authors__container {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 3rem;
    gap: 3rem;
}

.author {
    background: var(--color--bg);
    padding: 1rem;
    border-radius: var(--radius-3);
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    transition: var(--transition);
}

.author:hover {
    box-shadow: 0 1rem 1.5rem rgba(173, 170, 170, 0.815);
}

.author__avatar {
    width: 5rem;
    aspect-ratio: 1/1;
    border-radius: 50%;
    border: 0.3rem solid var(--color--bg);
    overflow: hidden;
}

.author__info p {
    margin-top: 1rem;
}

/*=============================================================profile===============================================*/
.profile__container {
    display: grid;
    place-items: center;
    text-align: center;
}

.profile__details {
    width: 100%;
}

.avatar__wrapper {
    width: 15rem;
    aspect-ratio: 1/1;
    position: relative;
    margin-inline: auto;
    margin-top: 1rem;
}

.profile__avatar {
    height: 100%;
    border-radius: 50%;
    border: 0.3rem solid var(--color-gray-300);
    overflow: hidden;
}

.avatar__form {
    height: 1rem;
}

.avatar__form input {
    visibility: hidden;
}

.avatar__form label, .profile__avatar-btn {
    background: var(--color--bg);
    color: var(--color-white);
    font-size: 1.3rem;
    position: absolute;
    right: 2rem;
    bottom: 1.4rem;
    width: 3rem;
    height: 3rem;
    display: grid;
    place-items: center;
    border-radius: 50%;
}

.profile__avatar-btn {
    cursor: pointer;
    padding: 0;
    background: var(--color-olive-100);
}

.profile__details h1 {
    margin-top: 1.5rem;
}

.form.profile__form {
    margin-top: 3rem;
    margin-inline: auto;
    width: var(--form-width);
}

/*===============================================================dashboard====================================*/
.dashboard__container {
    display: flex;
    flex-direction: column;
    grid-gap: 0.7rem;
    gap: 0.7rem;
}

.dashboard__post {
    display: flex;
    align-items: center;
    justify-content: space-between;
    grid-gap: 1rem;
    gap: 1rem;
    background: var(--color--bg);
    padding: 0.5rem 1.2rem;
    border-radius: var(--radius-3);
}

.dashboard__post-info {
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
    width: 100%;
    align-items: center;
}

.dashboard__post-thumbnail {
    width: 4rem;
    border-radius: var(--radius-1);
    overflow: hidden;
}

.dashboard__post-actions {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}


